/* Projects Page Styles */

.projects.page {
  background: var(--pixel-jungle-dark);
  min-height: 100vh;
}

.page-subtitle {
  text-align: center;
  color: var(--text-light);
  margin-bottom: 40px;
  font-size: 18px;
}

.projects-stats {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-bottom: 50px;
}

.project-stat {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  background: var(--card-bg);
  padding: 25px 30px;
  border: 4px solid var(--pixel-border);
  min-width: 120px;
  box-shadow: 
    inset -2px -2px 0 var(--pixel-jungle-dark),
    inset 2px 2px 0 var(--pixel-green-dark),
    0 0 0 1px var(--pixel-border);
  position: relative;
}

.project-stat::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--pixel-green);
  border-bottom: 2px solid var(--pixel-border);
}

.stat-number {
  font-family: 'Press Start 2P', 'Courier New', monospace;
  font-size: 28px;
  font-weight: bold;
  color: var(--pixel-yellow);
  text-shadow: 
    2px 2px 0 var(--pixel-border),
    -1px -1px 0 var(--pixel-border);
}

.stat-label {
  font-size: 14px;
  color: var(--text-light);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 500;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
}

.project-card {
  padding: 25px;
  position: relative;
  overflow: visible;
  border: 4px solid var(--pixel-border);
  background: var(--card-bg);
  box-shadow: 
    inset -3px -3px 0 var(--pixel-jungle-dark),
    inset 3px 3px 0 var(--pixel-green-dark),
    0 0 0 1px var(--pixel-border);
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--pixel-green);
  border-bottom: 2px solid var(--pixel-border);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 20px;
}

.project-icon {
  width: 50px;
  height: 50px;
  background: var(--pixel-green);
  border: 3px solid var(--pixel-border);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--pixel-text-dark);
  font-size: 20px;
  box-shadow: 
    inset -2px -2px 0 var(--pixel-green-dark),
    inset 2px 2px 0 var(--pixel-green-light);
}

.project-status {
  padding: 6px 12px;
  border: 2px solid var(--pixel-border);
  color: var(--pixel-text-dark);
  font-size: 11px;
  font-family: 'Courier New', monospace;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  background: var(--pixel-yellow);
  box-shadow: 
    inset -1px -1px 0 var(--pixel-yellow-dark),
    inset 1px 1px 0 var(--pixel-yellow-light);
}

.project-title {
  font-size: 22px;
  color: var(--text-color);
  margin-bottom: 15px;
  font-weight: 600;
}

.project-description {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 20px;
  font-size: 15px;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 25px;
}

.project-tag {
  background: var(--pixel-jungle-dark);
  color: var(--pixel-green-light);
  padding: 6px 12px;
  border: 2px solid var(--pixel-border);
  font-size: 12px;
  font-family: 'Courier New', monospace;
  font-weight: 500;
  transition: all 0.1s;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 
    inset -1px -1px 0 var(--pixel-jungle-dark),
    inset 1px 1px 0 var(--pixel-green-dark);
}

.project-tag:hover {
  background: var(--pixel-jungle-mid);
  color: var(--pixel-yellow);
  border-color: var(--pixel-green);
}

.project-actions {
  display: flex;
  gap: 15px;
}

.project-actions .btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 20px;
  font-size: 14px;
}

.project-actions .btn-primary {
  background: var(--pixel-green);
  color: var(--pixel-text-dark);
  border: 3px solid var(--pixel-border);
  box-shadow: 
    inset -3px -3px 0 var(--pixel-green-dark),
    inset 3px 3px 0 var(--pixel-green-light);
}

.project-actions .btn-primary:hover {
  background: var(--pixel-green-light);
  transform: translateY(-1px);
  box-shadow: 
    inset -2px -2px 0 var(--pixel-green),
    inset 2px 2px 0 var(--pixel-yellow);
}

.project-actions .btn-outline {
  border: 3px solid var(--pixel-green);
  color: var(--pixel-green-light);
  background: transparent;
  box-shadow: 0 0 0 1px var(--pixel-border);
}

.project-actions .btn-outline:hover {
  background: var(--pixel-green);
  color: var(--pixel-text-dark);
  transform: translateY(-1px);
  box-shadow: 
    inset -2px -2px 0 var(--pixel-green-dark),
    inset 2px 2px 0 var(--pixel-green-light);
}

/* Project Card Hover Effects - Quest Card Style */
.project-card:hover {
  transform: translateY(-2px);
  box-shadow: 
    inset -3px -3px 0 var(--pixel-jungle-dark),
    inset 3px 3px 0 var(--pixel-green),
    0 0 0 1px var(--pixel-border),
    0 8px 16px rgba(0, 0, 0, 0.4);
  border-color: var(--pixel-green);
}

.project-card:hover .project-icon {
  transform: scale(1.05);
  background: var(--pixel-green-light);
}

/* Status-specific styling */
.project-card[data-status="completed"] {
  border-left: 4px solid #4CAF50;
}

.project-card[data-status="in-progress"] {
  border-left: 4px solid #FF9800;
}

.project-card[data-status="planned"] {
  border-left: 4px solid #9C27B0;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 22px;
  }
}

@media (max-width: 968px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
  }
}

@media (max-width: 768px) {
  .projects-stats {
    flex-direction: column;
    gap: 20px;
    align-items: center;
  }

  .project-stat {
    flex-direction: row;
    justify-content: center;
    min-width: auto;
    width: 100%;
    max-width: 200px;
  }

  .projects-grid {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .project-actions {
    flex-direction: column;
  }

  .project-header {
    margin-bottom: 15px;
  }

  .project-icon {
    width: 45px;
    height: 45px;
    font-size: 18px;
  }

  .project-title {
    font-size: 20px;
  }
  
  .project-image {
    height: 180px;
  }
}

@media (max-width: 480px) {
  .project-card {
    padding: 18px;
  }

  .project-header {
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
  }

  .project-status {
    align-self: flex-end;
  }

  .project-icon {
    width: 40px;
    height: 40px;
    font-size: 16px;
  }

  .project-title {
    font-size: 18px;
  }

  .project-description {
    font-size: 14px;
  }

  .project-tags {
    gap: 6px;
  }

  .project-tag {
    font-size: 11px;
    padding: 4px 10px;
  }

  .stat-number {
    font-size: 24px;
  }

  .projects-stats {
    gap: 15px;
  }

  .project-stat {
    padding: 18px;
    flex-direction: column;
  }
  
  .project-image {
    height: 150px;
  }
  
  .project-actions .btn {
    width: 100%;
  }
}

@media (max-width: 360px) {
  .project-card {
    padding: 15px;
  }
  
  .project-title {
    font-size: 16px;
  }
  
  .project-description {
    font-size: 13px;
  }
}

