/* Project Image Styles */
.project-image {
  width: 100%;
  height: 200px;
  overflow: hidden;
  margin-bottom: 20px;
  border: 3px solid var(--pixel-border);
  box-shadow: 
    inset -2px -2px 0 var(--pixel-jungle-dark),
    inset 2px 2px 0 var(--pixel-green-dark);
  image-rendering: pixelated;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.project-card:hover .project-image img {
  transform: scale(1.05);
}

.project-actions .btn.full-width {
  width: 100%;
  flex: none;
}

