/* Clickable Card Styles */
.clickable-card {
  transition: all 0.2s ease;
  border: 4px solid var(--pixel-yellow) !important;
}

.clickable-card:hover {
  transform: translateY(-5px);
  box-shadow: 
    inset -3px -3px 0 var(--pixel-jungle-dark),
    inset 3px 3px 0 var(--pixel-yellow-dark),
    0 0 0 1px var(--pixel-border),
    0 0 25px rgba(244, 208, 63, 0.4);
}

.clickable-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-size: 16px;
  font-weight: bold;
  color: var(--pixel-yellow);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 15px;
  background: rgba(167, 139, 250, 0.1);
  border-radius: 8px;
  border: 2px solid var(--pixel-yellow);
}

.clickable-indicator svg {
  font-size: 20px;
}

/* Minimal Card Styles */
.minimal-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 350px;
  padding: 50px 30px;
  text-align: center;
  background: linear-gradient(135deg, var(--card-bg) 0%, rgba(167, 139, 250, 0.1) 100%);
  border: none;
  box-shadow: none;
}

.minimal-card::before {
  display: none;
}

.minimal-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.github-icon-large {
  font-size: 100px;
  color: var(--pixel-yellow);
  transition: all 0.3s ease;
  filter: drop-shadow(0 0 15px rgba(244, 208, 63, 0.4));
  animation: pulse 2s infinite;
}

.minimal-card:hover .github-icon-large {
  transform: scale(1.2);
  color: var(--pixel-green);
  filter: drop-shadow(0 0 25px rgba(76, 175, 80, 0.6));
}

.see-more-text {
  font-size: 24px;
  font-weight: bold;
  color: var(--text-light);
  text-transform: uppercase;
  letter-spacing: 3px;
  font-family: 'Press Start 2P', 'Courier New', monospace;
  text-shadow: 3px 3px 0 var(--pixel-border);
  transition: all 0.3s ease;
}

.minimal-card:hover .see-more-text {
  color: var(--pixel-yellow);
  text-shadow: 3px 3px 0 var(--pixel-green);
  transform: translateY(-5px);
}

/* Responsive adjustments for minimal cards */
@media (max-width: 768px) {
  .minimal-card {
    min-height: 280px;
    padding: 40px 25px;
  }
  
  .github-icon-large {
    font-size: 80px;
  }
  
  .see-more-text {
    font-size: 20px;
    letter-spacing: 2px;
  }
}

@media (max-width: 480px) {
  .minimal-card {
    min-height: 240px;
    padding: 30px 20px;
  }
  
  .github-icon-large {
    font-size: 70px;
  }
  
  .see-more-text {
    font-size: 18px;
    letter-spacing: 1px;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    filter: drop-shadow(0 0 15px rgba(244, 208, 63, 0.4));
  }
  50% {
    transform: scale(1.05);
    filter: drop-shadow(0 0 20px rgba(244, 208, 63, 0.6));
  }
  100% {
    transform: scale(1);
    filter: drop-shadow(0 0 15px rgba(244, 208, 63, 0.4));
  }
}